import React, { useState, useRef } from 'react';
import PropTypes from 'prop-types';
import * as Ant from 'antd';
import './index.scss';

/**
 * Utils
 */
const hasItem = (arr, key) => Array.isArray(arr) && arr.includes(key);
const hasLength = (arr) => Array.isArray(arr) && arr.length > 0;
const addUniqueClass = (restProps, name) => {
  const { className = '' } = restProps;
  restProps.className = `ant-plus-${name} ${className}`;
};

/**
 * tip -> placeholder
 */
const inputList = ['Input', 'AutoComplete', 'Transfer'].map((e) => `AntPlus.${e}`);
const selectList = ['Select', 'Cascader', 'TreeSelect'].map((e) => `AntPlus.${e}`);
const displayNameList = [...inputList, ...selectList];
const tipList = ['short', 'full'];

/**
 * Form config and rules
 */
const defaultConfig = {
  // placeholder
  input: '请输入',
  select: '请选择',
  // message
  required: '{label}不得为空',
  type: '{label}格式有误',
  max: '不得超过 {max} 个字',
  min: '不得少于 {min} 个字',
};
const ruleMap = {
  // required
  required: { required: true },
  // type
  string: { type: 'string', whitespace: true },
  number: { pattern: /^\d+$/, whitespace: true },
  array: { type: 'array' },
  email: { type: 'email', whitespace: true },
  phone: { pattern: /^1[3456789]\d{9}$/, whitespace: true },
  id: { pattern: /^\d+x?$/i, whitespace: true },
  // num
  max: {},
  min: {},
};
const numRuleList = ['max', 'min'];
const restRuleList = Object.keys(ruleMap).filter((e) => !numRuleList.includes(e));

/**
 * Form.Item props
 */
const layoutPropList = [
  'colon',
  'htmlFor',
  'noStyle',
  'label',
  'labelAlign',
  'labelCol',
  'shouldUpdate',
  'wrapperCol',
];
const validatePropList = [
  'dependencies',
  'extra',
  'getValueFromEvent',
  'hasFeedback',
  'help',
  'name',
  'normalize',
  'required',
  'rules',
  'trigger',
  'validateFirst',
  'validateStatus',
  'validateTrigger',
  'valuePropName',
  'getValueProps', // rc-field-form
  'initialValue', // rc-field-form
];
const miscPropList = [
  'itemClass',
  'itemStyle',
  'before',
  'after',
  'hide', // hide but has value
];
const labelOmitList = [...validatePropList, ...miscPropList];
const namePickOmitList = [...layoutPropList, ...validatePropList];

const getItemProps = (obj, pickOmitList, omitList) => {
  const itemProps = {};
  const innerProps = { ...obj };

  pickOmitList.forEach((key) => {
    if (obj[key] !== undefined) itemProps[key] = obj[key]; // pick
    delete innerProps[key]; // omit
  });
  omitList.forEach((key) => {
    delete innerProps[key]; // omit
  });

  return { itemProps, innerProps };
};

/**
 * Form - Ant Design Form 组件增强版本
 * https://ant.design/fns/form-cn/
 */
function Form(props) {
  const { config: newConfig, disabledFields, ...formProps } = props;
  const config = useRef({ ...defaultConfig, ...newConfig });

  /**
   * 生成校验信息
   */
  const createRules = (rules, label = '') => {
    if (!Array.isArray(rules)) return rules;

    return rules.map((rule) => {
      if (typeof rule !== 'string') return rule;

      // e.g. 'max=10'
      if (rule.includes('=')) {
        const [key, val] = rule.split('=');
        if (!numRuleList.includes(key)) return rule;
        return { [key]: +val, message: config.current[key].replace(`{${key}}`, val) };
      }

      // e.g. 'required'
      if (!restRuleList.includes(rule)) return rule;
      const key = rule === 'required' ? rule : 'type';
      return { ...ruleMap[rule], message: config.current[key].replace('{label}', label) };
    });
  };

  /**
   * 获取 tip 与 disabled 属性
   */
  const getNodeTip = (node, label = '') => {
    const { displayName } = node.type;
    const { tip } = node.props;

    // `tip` 属性
    if (
      typeof displayName === 'string' &&
      typeof tip === 'string' &&
      displayNameList.includes(displayName) &&
      tipList.includes(tip)
    ) {
      const isSelect = selectList.includes(displayName);
      const shortTip = isSelect ? config.current.select : config.current.input;
      const tipWords = tip === 'short' ? shortTip : `${shortTip}${label}`;
      return { ...node, props: { ...node.props, tip: tipWords } };
    }

    return node;
  };
  const getNodeTipDisabled = (node, label, name) => {
    const newNode = getNodeTip(node, label);

    // `disabled` 属性
    if (disabledFields === 'all' || (typeof name === 'string' && hasItem(disabledFields, name))) {
      return { ...newNode, props: { ...newNode.props, disabled: true } };
    }
    return newNode;
  };

  /**
   * 核心渲染方法
   */
  const renderNodes = (nodes) => {
    if (typeof nodes !== 'object' || nodes === null) return nodes;

    return React.Children.map(nodes, (node) => {
      if (typeof node !== 'object' || node === null || !node.props) return node;

      const { children, ...nodeProps } = node.props;
      const { label, name } = nodeProps;

      // 1. ``
      if (label === undefined && name === undefined) {
        const normalNode = { ...node, props: { ...nodeProps, children: renderNodes(children) } };
        return getNodeTip(normalNode);
      }

      // 2. `label`
      if (name === undefined) {
        const { itemClass = '', itemStyle, before, after } = nodeProps;

        const { itemProps, innerProps } = getItemProps(nodeProps, layoutPropList, labelOmitList);
        const innerNode = { ...node, props: { ...innerProps, children: renderNodes(children) } };

        return (
          <Ant.Form.Item className={itemClass} style={itemStyle} {...itemProps}>
            {renderNodes(before)}
            {getNodeTip(innerNode, label)}
            {renderNodes(after)}
          </Ant.Form.Item>
        );
      }

      // 3. `name` || `label` `name`
      const { itemClass = '', itemStyle, hide, rules: oldR, validateTrigger: oldVt } = nodeProps;

      const rules = createRules(oldR, label);
      const validateTrigger = oldVt || hasItem(oldR, 'phone') ? 'onBlur' : 'onChange';
      const newNodeProps = { ...nodeProps, rules, validateTrigger };

      const { itemProps, innerProps } = getItemProps(newNodeProps, namePickOmitList, miscPropList);
      const innerNode = { ...node, props: innerProps };

      return (
        <Ant.Form.Item
          className={`form-item-${name} ${itemClass}`}
          style={hide === true ? { display: 'none' } : itemStyle}
          {...itemProps}
        >
          {getNodeTipDisabled(innerNode, label, name)}
        </Ant.Form.Item>
      );
    });
  };

  /**
   * Form
   */
  const { data, children, ...restProps } = formProps; // eslint-disable-line
  addUniqueClass(restProps, 'form');

  return (
    <Ant.Form initialValues={data} {...restProps}>
      {renderNodes(children)}
    </Ant.Form>
  );
}

Form.propTypes = {
  /** initialValues 简写。e.g. { name1: value1, name2: value2 } */
  data: PropTypes.object,
  /** placeholder 与校验提示，详见下文 config 表格。e.g. { required: 'Oops, {label} is needed' } */
  config: PropTypes.object,
  /** 禁用的表单域，传入 name 组成的数组。全部禁用传入字符串 "all" */
  disabledFields: PropTypes.array,
};

Form.defaultProps = {};

/**
 * Input - Ant Design Input (TextArea) 组件增强版本
 * https://ant.design/fns/input-cn/
 */
function Input(props) {
  const { max, tip, auto, textarea, rows, ...restProps } = props;
  const { disabled } = restProps;

  addUniqueClass(restProps, 'input');

  const renderInput = (inputProps) => {
    if (textarea !== true) {
      return <Ant.Input placeholder={tip} autoComplete={auto} {...inputProps} />;
    }
    return <Ant.Input.TextArea placeholder={tip} autoSize={{ minRows: rows }} {...inputProps} />;
  };

  const noCount = typeof max !== 'number' || disabled === true;

  const [count, setCount] = useState(() => {
    if (noCount) return 0;
    const { defaultValue, value } = restProps;
    if (typeof value === 'string') return value.length;
    if (typeof defaultValue === 'string') return defaultValue.length;
    return 0;
  });

  // no count
  if (noCount) return renderInput(restProps);

  // has count
  const { onChange } = restProps;
  restProps.onChange = (event) => {
    const { value } = event.target;
    if (typeof value === 'string') setCount(value.length);
    if (typeof onChange === 'function') return onChange(event);
  };

  return (
    <div className="ant-plus-input-wrapper">
      {renderInput(restProps)}
      <span className={`count ${count <= max ? '' : 'red'}`}>
        {count} | {max}
      </span>
    </div>
  );
}

Input.propTypes = {
  /** 最大可输入字符数（传入则显示字符计数器）*/
  max: PropTypes.number,
  /** placeholder 简写，可传入 "short" 或 "full"（AntPlus Form 内有效）。转义："short" => "请输入", "full" => "请输入XX", "其它" => "其它" */
  tip: PropTypes.string,
  /** autoComplete 简写（关闭需传入 "off"）*/
  auto: PropTypes.string,
  /** 默认为 Input 组件，若传入 textarea，则为 TextArea 组件 */
  textarea: PropTypes.bool,
  /** TextArea 的输入框行高 */
  rows: PropTypes.number,
};

Input.defaultProps = {
  textarea: false,
  rows: 2,
};

/**
 * AutoComplete - Ant Design AutoComplete 组件增强版本
 * https://ant.design/fns/auto-complete-cn/
 */
function AutoComplete(props) {
  const { data, tip, search, clear, ...restProps } = props;

  let searchProps;
  if (search === true) {
    searchProps = { filterOption: (val, option) => option.value.includes(val) };
  }

  addUniqueClass(restProps, 'auto-complete');

  return (
    <Ant.AutoComplete
      options={data}
      placeholder={tip}
      {...searchProps}
      allowClear={clear}
      {...restProps}
    />
  );
}

AutoComplete.propTypes = {
  /** options 简写 */
  data: PropTypes.array,
  /** placeholder 简写，可传入 "short" 或 "full"（AntPlus Form 内有效）。转义："short" => "请输入", "full" => "请输入XX", "其它" => "其它" */
  tip: PropTypes.string,
  /** 是否可搜索 */
  search: PropTypes.bool,
  /** allowClear 简写 */
  clear: PropTypes.bool,
};

AutoComplete.defaultProps = {
  data: [],
  search: true,
  clear: false,
};

/**
 * Select - Ant Design Select 组件增强版本
 * https://ant.design/fns/select-cn/
 */
function Select(props) {
  const { data, keys, tip, search, clear, empty, ...restProps } = props;
  const [value = 'value', label = 'label'] = keys;

  let searchProps;
  if (search === true) {
    searchProps = {
      showSearch: true,
      filterOption: (val, option) => option.children.includes(val),
    };
  }

  addUniqueClass(restProps, 'select');

  return (
    <Ant.Select
      placeholder={tip}
      {...searchProps}
      allowClear={clear}
      notFoundContent={empty}
      {...restProps}
    >
      {data.map((item) => (
        <Ant.Select.Option key={item[label]} value={item[value]} disabled={item.disabled}>
          {item[label]}
        </Ant.Select.Option>
      ))}
    </Ant.Select>
  );
}

Select.propTypes = {
  /** 列表数据源 */
  data: PropTypes.array,
  /** 当数据源的键不是 "value" (值) "label" (名称) 时传入。e.g. 数据源 [{ val: 1, text: 'A'}, { val: 2, text: 'B'}]，则传入 ['val', 'text'] */
  keys: PropTypes.array,
  /** placeholder 简写，可传入 "short" 或 "full"（AntPlus Form 内有效）。转义："short" => "请选择", "full" => "请选择XX", "其它" => "其它" */
  tip: PropTypes.string,
  /** 是否可搜索 */
  search: PropTypes.bool,
  /** allowClear 简写 */
  clear: PropTypes.bool,
  /** notFoundContent 简写 */
  empty: PropTypes.string,
};

Select.defaultProps = {
  data: [],
  keys: ['value', 'label'],
  search: false,
  clear: false,
};

/**
 * Transfer - Ant Design Transfer 组件增强版本
 * https://ant.design/fns/transfer-cn/
 */
function Transfer(props) {
  const { data, title, search, unit, searchTip, empty, ...restProps } = props;

  let searchProps;
  if (search === true) {
    searchProps = {
      showSearch: true,
      filterOption: (val, option) => `${option.title}${option.description || ''}`.includes(val),
    };
  }

  addUniqueClass(restProps, 'transfer');

  const { targetKeys, value } = restProps;
  restProps.targetKeys = targetKeys || value;

  return (
    <Ant.Transfer
      dataSource={data}
      titles={[`未选择${title}`, `已选择${title}`]}
      {...searchProps}
      locale={{
        itemsUnit: unit,
        itemUnit: unit,
        searchPlaceholder: searchTip,
        notFoundContent: empty,
      }}
      {...restProps}
    />
  );
}

Transfer.propTypes = {
  /** dataSource 简写 */
  data: PropTypes.array,
  /** "未选择XX" "已选择XX" 的 "XX" 文案 */
  title: PropTypes.string,
  /** 是否可搜索 */
  search: PropTypes.bool,
  /** local.itemUnit 与 local.itemsUnits 简写，默认：'项' */
  unit: PropTypes.string,
  /** locale.searchPlaceholder 简写（不支持 "short" "full"） */
  searchTip: PropTypes.string,
  /** locale.notFoundContent 简写 */
  empty: PropTypes.string,
};

Transfer.defaultProps = {
  data: [],
  title: '',
  search: false,
  unit: '项',
  searchTip: '搜索',
  render: (item) => item.title,
};

/**
 * Cascader - Ant Design Cascader 组件增强版本
 * https://ant.design/fns/cascader-cn/
 */
function Cascader(props) {
  const { data, keys, search, clear, empty, tip, last, ...restProps } = props;
  const [valueKey = 'value', labelKey = 'label', childrenKey = 'children'] = keys;

  const valPathMap = useRef({});
  const prevLength = useRef(0);

  if (last === true) {
    if (prevLength.current !== data.length) {
      prevLength.current = data.length;

      const travelOptions = (data, valList = []) => {
        data.forEach((item) => {
          const { [valueKey]: val, [childrenKey]: children } = item;
          const selfValList = valList.concat(val);
          if (hasLength(children)) return travelOptions(children, selfValList);
          valPathMap.current[val] = selfValList;
        });
      };
      travelOptions(data);
    }

    const { defaultValue, value, onChange } = restProps;
    if (value !== undefined) {
      restProps.value = valPathMap.current[value];
    } else if (defaultValue !== undefined) {
      restProps.defaultValue = valPathMap.current[defaultValue];
    }
    if (typeof onChange === 'function') {
      restProps.onChange = (val) => onChange(val[val.length - 1]);
    }
  }

  let searchProps;
  if (search === true) {
    searchProps = {
      showSearch: { filter: (val, path) => path.some((option) => option[labelKey].includes(val)) },
    };
  }

  addUniqueClass(restProps, 'cascader');

  return (
    <Ant.Cascader
      options={data}
      fieldNames={{ value: valueKey, label: labelKey, children: childrenKey }}
      placeholder={tip}
      {...searchProps}
      allowClear={clear}
      notFoundContent={empty}
      {...restProps}
    />
  );
}

Cascader.propTypes = {
  /** options 简写 */
  data: PropTypes.array,
  /** 当数据源的键不是 "value" "label" "children" 时传入 */
  keys: PropTypes.array,
  /** placeholder 简写（不支持 "short" "full"） */
  tip: PropTypes.string,
  /** 是否可搜索 */
  search: PropTypes.bool,
  /** allowClear 简写 */
  clear: PropTypes.bool,
  /** notFoundContent 简写 */
  empty: PropTypes.string,
  /** value 取数组最后一个值，默认为整体数组（AntPlus Form 内有效）*/
  last: PropTypes.bool,
};

Cascader.defaultProps = {
  data: [],
  keys: ['value', 'label', 'children'],
  tip: '请选择',
  search: false,
  clear: true,
  last: false,
};

/**
 * TreeSelect - Ant Design TreeSelect 组件增强版本
 * https://ant.design/fns/tree-select-cn/
 */

function TreeSelect(props) {
  const {
    data,
    keys,
    tip,
    search,
    clear,
    empty,
    checkbox,
    expandAll,
    expandKeys,
    showType,
    ...restProps
  } = props;
  const [valueKey = 'value', titleKey = 'title', childrenKey = 'children'] = keys;

  const treeData = useRef([]);

  if (treeData.current.length !== data.length) {
    if (valueKey === 'value' && titleKey === 'title' && childrenKey === 'children') {
      treeData.current = data;
    } else {
      const getNewData = (data) => {
        return data.map((item) => {
          const { [valueKey]: value, [titleKey]: title, [childrenKey]: children } = item;
          const newItem = { value, title };
          if (hasLength(children)) newItem.children = getNewData(children);
          return newItem;
        });
      };
      treeData.current = getNewData(data);
    }
  }

  if (treeData.length === 0) delete restProps.value;

  let searchProps;
  if (search === true) {
    searchProps = {
      showSearch: true,
      filterTreeNode: (val, node) => new RegExp(val, 'i').test(`${node.value}${node.title}`),
    };
  }

  addUniqueClass(restProps, 'tree-select');

  return (
    <Ant.TreeSelect
      treeData={treeData.current}
      placeholder={tip}
      {...searchProps}
      allowClear={clear}
      notFoundContent={empty}
      treeCheckable={checkbox}
      treeDefaultExpandAll={expandAll}
      treeDefaultExpandedKeys={expandKeys}
      showCheckedStrategy={showType}
      {...restProps}
    />
  );
}

TreeSelect.propTypes = {
  /** treeData 简写 */
  data: PropTypes.array,
  /** 当数据源的键不是 "value" "title" "children" 时传入 */
  keys: PropTypes.array,
  /** placeholder 简写（不支持 "short" "full"） */
  tip: PropTypes.string,
  /** 是否可搜索 */
  search: PropTypes.bool,
  /** allowClear 简写 */
  clear: PropTypes.bool,
  /** notFoundContent 简写 */
  empty: PropTypes.string,
  /** treeCheckable 简写 */
  checkbox: PropTypes.bool,
  /** treeDefaultExpandAll 简写 */
  expandAll: PropTypes.bool,
  /** treeDefaultExpandedKeys 简写 */
  expandKeys: PropTypes.array,
  /** showCheckedStrategy 简写 */
  showType: PropTypes.oneOf([
    Ant.TreeSelect.SHOW_ALL,
    Ant.TreeSelect.SHOW_PARENT,
    Ant.TreeSelect.SHOW_CHILD,
  ]),
};

TreeSelect.defaultProps = {
  data: [],
  keys: ['value', 'title', 'children'],
  tip: '请选择',
  search: false,
  clear: false,
  checkbox: false,
  expandAll: false,
  showType: Ant.TreeSelect.SHOW_CHILD,
};

/**
 * Button
 * 未做任何封装，方便 Form 中使用
 */
const Button = Ant.Button;

/**
 * exports
 */
const AntPlus = { Form, Input, AutoComplete, Select, Transfer, Cascader, TreeSelect };
const omitKeys = ['propTypes', 'defaultProps'];

const copyOriginalKeys = () => {
  const { Form, Input, AutoComplete, Select, Transfer, Cascader, TreeSelect } = Ant;
  const AntDesign = { Form, Input, AutoComplete, Select, Transfer, Cascader, TreeSelect };

  Object.entries(AntPlus).forEach(([name, fn]) => {
    const obj = AntDesign[name];
    Object.entries(obj).forEach(([key, val]) => {
      if (!omitKeys.includes(key)) fn[key] = val;
    });
    fn.displayName = `AntPlus.${name}`;
  });
};

copyOriginalKeys();

export { Form, Input, AutoComplete, Select, Transfer, Cascader, TreeSelect, Button };
